:root {
    --white: hsl(0, 0%, 100%, 1);
    --whiteLight: hsla(0, 11%, 86%, 0.788);
    --gray: hsla(0, 0%, 85%, 1);
    --grayLight: hsla(0, 0%, 47%, 1);
    --grayDark: hsl(0, 0%, 51%);
    --black: hsl(0, 0%, 0%, 1);
    --darkbackground: hsla(0, 0%, 0%, 0.7);
    --blue: hsl(205, 84%, 55%, 1);
    --blueDark: hsla(200, 67%, 50%, 0.396);
    --colorBlue: #2a9bd4;
    --colorBlack: #000;
    --colorParagraph: #082a3b;
    --bluecontact: hsl(216, 62%, 25%);
    --colorWhite: #fff;
    --bluecontact: hsl(216, 62%, 25%);
}
